<template>
  <PopUp :flow="twStatus">
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closeTw">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe649;</i>
          <span class="title-span">{{ $t("history.title") }}</span>
        </div>
        <div class="content">
          <div class="msg">
            <span
              >{{ currentRecord.roomAndBed }} &emsp;{{
                currentRecord.patientName
              }}
              &emsp;{{ currentRecord.patientCaseNum }}</span
            >
          </div>
          <div
            class="zx-pic"
            id="myChart"
            style="width:798px;height:355px;"
          ></div>
          <div class="">
            <div class="history-tj-abtm">
              <span v-if="tj.highesTemperature && hss == true"
                >{{ $t("history.zgwd") }}: {{ tj.highesTemperature }}℃</span
              >
              <span v-if="tj.highesTemperature && hss == false"
                >{{ $t("history.zgwd") }}: {{ tj.highesTemperature }}℉</span
              >
              <span v-if="!tj.highesTemperature"
                >{{ $t("history.zgwd") }}: --</span
              >
              <!-- <span
                >{{ $t("history.cxyj") }}: {{ tj.times }}
                {{ $t("history.time") }}</span
              > -->
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click="closeTw">{{
            $t("button.confirm")
          }}</a>
          <a href="javascript:void(0)" class="off" @click="closeTw">{{
            $t("button.close")
          }}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from "../../components/popUp";
import { getRule, getCurrentHistory } from "@/api/history";

var that = {};
export default {
  name: "tjzc",
  data() {
    return {
      rule: {
        highAlert: "",
        lowAlert: ""
      },
      timesList: [],
      dataList: [],
      temperatureList: [],
      tj: {
        highesTemperature: "",
        times: ""
      },
      low: "",
      high: "",
      fatter: "",
      hss: that.$store.state.monitor.selectTem
    };
  },
  props: {
    twStatus: {
      type: Boolean
    },
    departmentId: {
      type: Number
    },
    currentRecord: {
      type: Object
    }
  },
  beforeCreate() {
    that = this;
  },
  mounted: function() {
    that.getRuleFn();
    if (that.hss) {
      that.low = 32;
      that.high = 41;
      that.fatter = "{value}℃";
      that.cj = 0.5;
    } else {
      that.low = 89.6;
      that.high = 105.8;
      that.fatter = "{value}℉";
      that.cj = 0.5;
    }
  },
  methods: {
    getCurrentHistoryFn() {
      if (that.$store.state.monitor.selectTem) {
        var temType = 0;
      } else {
        var temType = 1;
      }
      var data = {
        type: 0,
        recordId: that.currentRecord.recordId,
        temType: temType
      };
      getCurrentHistory(data).then(res => {
        if (res.code == 200 && res.success) {
          if (res.data == "") {
            that.twHistoryData = [];
          } else {
            that.twHistoryData = res.data.history;
          }
          that.twHistoryData.map(function(item) {
            item.map(function(it) {
              var i = that.timestampToTime(it.timestamp);
              it.timestamp = i;
              console.log(it.timestamp.slice(5, 16));
              that.timesList.push(it.timestamp.slice(5, 16));
              that.temperatureList.push(it.temperature);
              that.dataList.push(it);
            });
          });
          console.log(that.rule);
          console.log(that.timesList);
          that.drawLineTW();
          let max = that.temperatureList[0];
          that.temperatureList.forEach(item => (max = item > max ? item : max));
          that.tj.highesTemperature = max;
          var flag = 0;
          for (var i = 0; i < that.temperatureList.length; i++) {
            if (
              Number(that.temperatureList[i]) <= Number(that.rule.highAlert) &&
              Number(that.temperatureList[i + 1]) > Number(that.rule.highAlert)
            ) {
              flag = flag + 1;
            }
          }
          that.tj.times = flag;
        }
      });
    },
    closeTw(twStatus) {
      that.$emit("gettwStatus", !that.twStatus);
    },
    /*获取规则*/
    getRuleFn() {
      var data = {
        departmentId: that.departmentId
      };
      getRule(data).then(res => {
        if (res.code == 200 && res.success) {
          res.data.map(function(item) {
            if (item.type == 0) {
              that.rule = {
                highAlert: item.highAlert,
                lowAlert: item.lowAlert
              };
            }
          });
          that.getCurrentHistoryFn();
        }
      });
    },
    /*绘制图表*/
    drawLineTW() {
      let myChart = that.$echarts.init(document.getElementById("myChart"));
      myChart.clear();
      myChart.setOption({
        tooltip: {
          trigger: "axis"
          // formatter: "{b}"+"<br>"+"{c}℃",
          // formatter:'{value}'
        },
        grid: {
          //图标留白区域
          top: "14px",
          left: "60px",
          right: "60px",
          bottom: "33px"
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          axisLine: { symbol: ["none", "arrow"] },
          data: that.timesList,
          axisLabel: {
            rotate: 0
          }
        },
        yAxis: {
          min: that.low,
          max: that.high,
          interval: that.cj,
          axisLabel: {
            formatter: that.fatter
          },
          axisLine: { symbol: ["none", "arrow"] }
        },
        dataZoom: [
          {
            start: 0,
            end: 50
          },
          {
            type: "inside",
            start: 0,
            end: 50
          },
          {
            start: 0,
            end: 50
          }
        ],
        series: [
          {
            name: that.$t("history.temper"),
            data: that.temperatureList,
            symbol: "none",
            sampling: "average",
            type: "line",
            areaStyle: {
              color: {
                type: "linear",
                x: 1,
                y: 0,
                x2: 0,
                y2: 0,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(94,119,255,0.65)"
                  },
                  {
                    offset: 1,
                    color: "rgba(94,119,255,1)"
                  }
                ],
                global: false
              }
            },
            lineStyle: {
              color: "rgba(94,119,255,1)",
              width: 0
            },
            itemStyle: {
              normal: {
                borderWidth: 0,
                color: "rgba(94,119,255,1)",
                borderColor: "rgba(94,119,255,1)"
              }
            },
            markLine: {
              itemStyle: {
                normal: {
                  borderWidth: 1,
                  lineStyle: {
                    type: "dotted",
                    width: 2
                  }
                }
              },
              data: [
                // {
                //     name: that.$t('monitor.status.high'),
                //     lineStyle: {
                //        color:'#E02020'
                //    },
                //    label: {
                //      formatter: that.rule.highAlert+'℃',
                //        textStyle: {
                //            fontSize: 16,
                //            fontWeight: "bolder",
                //        },
                //    },
                //     yAxis: that.rule.highAlert,
                // },
                // {
                //     name: that.$t('monitor.status.lowTep'),
                //     lineStyle: {
                //        color: '#007BB6'
                //    },
                //    label: {
                //      formatter:  that.rule.lowAlert+'℃',
                //        textStyle: {
                //            fontSize: 16,
                //            fontWeight: "bolder",
                //        },
                //    },
                //     yAxis: that.rule.lowAlert
                // },
              ]
            }
          }
        ]
      });
    }
  },
  components: {
    PopUp: popUp
  }
};
</script>
<style scoped lang="scss">
.content {
  margin: 0 42px;
  flex-direction: column;
  .msg {
    display: flex;
    span {
      font-size: 18px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(153, 153, 153, 1);
      text-align: left;
      padding-left: 22px;
      margin: 18px 0 13px;
    }
  }
  .select-pd {
    padding-left: 22px;
    display: flex;
    margin-bottom: 14px;
    span {
      font-size: 18px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(51, 51, 51, 1);
      margin-right: 10px;
      line-height: 30px;
    }
    a {
      display: inline-block;
      padding: 6px 24px;
      border-radius: 17px;
      border: 1px solid rgba(24, 144, 255, 1);
      color: #1890ff;
      margin-right: 22px;
    }
    .lsjl-active {
      background: rgba(24, 144, 255, 1);
      color: #fff;
    }
  }
  .history-tj-abtm {
    padding: 16px 0 30px 0;
    width: 600px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    span {
      font-size: 18px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(38, 38, 38, 1);
    }
  }
  .history-tj-xlhx {
    width: 500px;
    margin: 3px auto 0 auto;
    .xlhx-row {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;
      .ys {
        display: flex;
        justify-content: space-between;
        i {
          width: 25px;
          height: 1px;
          display: block;
          margin-top: 8px;
        }
        .xl {
          background: #7c6af2;
        }
        .hx {
          background: #5c89ff;
        }
        span {
          display: block;
          width: 60px;
          font-size: 16px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: rgba(40, 45, 50, 1);
        }
      }
    }
  }
}
</style>



