<template>
  <PopUp :flow='xdStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXd'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe649;</i>
          <span class="title-span">{{$t("history.title")}}</span>
        </div>
        <div class="content">
          <div class='msg'>
            <span>{{currentRecord.roomAndBed}} &emsp;{{currentRecord.patientName}} &emsp;{{currentRecord.patientCaseNum}}</span>
          </div>
          <div class="zx-pic" id='myChart' style="width:798px;height:355px;"></div>

          <div class="">
            <div class="history-tj-xlhx">
              <div class="xlhx-row">
                <div class="ys">
                  <i></i>
                  <span class='ys-span'></span>
                </div>
                <span>{{$t("history.bigest")}}</span>
                <span>{{$t("history.smallest")}}</span>
                <span>{{$t("history.average")}}</span>
              </div>
              <div class="xlhx-row">
                <div class="ys">
                  <i class='xl'></i>
                  <span class='ys-span'>{{$t("history.xl")}}</span>
                </div>
                <!-- <span>{{tj.heartRate.max==null?'--':tj.heartRate.max}}</span>
                <span>{{tj.heartRate.min==null?'--':tj.heartRate.min}}</span>
                <span>{{tj.heartRate.average==0?'--':tj.heartRate.average}}</span> -->
                <span>{{huxianshiZhi.xinLvMax}}</span>
                <span>{{huxianshiZhi.xinLvMin}}</span>
                <span>{{huxianshiZhi.xinLvAvg}}</span>
              </div>
              <div class="xlhx-row">
                <div class="ys">
                  <i class='hx'></i>
                  <span class='ys-span'>{{$t("history.hxl")}}</span>
                </div>
                <!-- <span>{{tj.respiratoryRate.max==null?'--':tj.respiratoryRate.max}}</span>
                <span>{{tj.respiratoryRate.min==null?'--':tj.respiratoryRate.min}}</span>
                <span>{{tj.respiratoryRate.average==0?'--':tj.respiratoryRate.average}}</span> -->
                <span>{{huxianshiZhi.huxiMax}}</span>
                <span>{{huxianshiZhi.huxiMin}}</span>
                <span>{{huxianshiZhi.huxiAvg}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='closeXd'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXd'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { getCurrentHistory } from '@/api/history'
var that= {};
  export default {
    name: 'tjzc',
      data(){
          return {
            timesList:[],
            // tpecHeartRateList:[],
            // tpecRespiratoryRateList:[],
            // tj:{
            //   heartRate:{
            //     max:'',
            //     min:'',
            //     average:''
            //   },
            //   respiratoryRate:{
            //     max:'',
            //     min:'',
            //     average:''
            //   }
            // },
            xinLvList: [],
            huxiLvList: [],
            huxianshiZhi: {
              huxiMax: "",
              huxiMin: "",
              huxiAvg: "",
              xinLvMax: "",
              xinLvMin: "",
              xinLvAvg: ""
            },
          }
      },
      props:{
        xdStatus:{
          type:Boolean
        },
        departmentId: {
          type: Number
        },
        currentRecord:{
          type:Object
        },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.getCurrentHistoryFn();
      },
      methods:{

        /*除法*/
      // accDiv(data1,data2){
      //   let t1=0,t2=0,r1,r2;
      //  // 获取每个参数的小数的位数
      //  try{t1=data1.toString().split(".")[1].length}catch(e){}
      //  try{t2=data2.toString().split(".")[1].length}catch(e){}
      //  // 把所有参数的小数点去掉转为整数
      //  r1=Number(data1.toString().replace(".",""));
      //  r2=Number(data2.toString().replace(".",""));
      //  return (r1/r2)*Math.pow(10,t2-t1);
      // },
      average(arr) {
        //求数组平均值
        var len = arr.length;
        var sum = 0;
        for (var i = 0; i < len; i++) {
          sum += arr[i];
        }
        return Math.floor(sum / len);
      },
        getCurrentHistoryFn(){
          var arr = [];
          var data={
            type:1,
            recordId:that.currentRecord.recordId
          }
          getCurrentHistory(data).then((res)=>{
            if(res.code==200 && res.success){
              var arrTime = [];
              var filterList = [];
              that.xinLvList = [];
              that.huxiLvList = [];
              that.tpecTimesList = [];
              console.log(res.data);
              res.data.history.map(item =>{
                item.map(it =>{
                  if(it.heartRate && it.respiratoryRate){
                    that.xinLvList.push(it.heartRate);
                    that.huxiLvList.push(it.respiratoryRate);
                    arrTime.push(it.timestamp);
                  }
                })
              })
              // filterList = res.data.history[0].filter(item => {
              //   return item.heartRate && item.respiratoryRate;
              // });
              // console.log(filterList);
              // filterList.map(item => {
              //   arrTime.push(item.timestamp);
              //   that.xinLvList.push(item.heartRate);
              //   that.huxiLvList.push(item.respiratoryRate);
              // });
              arrTime.map(item => {
                that.tpecTimesList.push(that.timestampToTime(item).slice(5, 16));
              });
              console.log(that.xinLvList);
              console.log(that.huxiLvList);
              console.log(that.tpecTimesList);
              if (that.huxiLvList.length > 0) {
                that.huxianshiZhi.huxiMax = Math.max(...that.huxiLvList);
                that.huxianshiZhi.huxiMin = Math.min(...that.huxiLvList);
                that.huxianshiZhi.huxiAvg = Math.round(
                  that.average(that.huxiLvList)
                );
              } else {
                that.huxianshiZhi.huxiMax = "--";
                that.huxianshiZhi.huxiMin = "--";
                that.huxianshiZhi.huxiAvg = "--";
              }
              if (that.xinLvList.length > 0) {
                that.huxianshiZhi.xinLvMax = Math.max(...that.xinLvList);
                that.huxianshiZhi.xinLvMin = Math.min(...that.xinLvList);
                that.huxianshiZhi.xinLvAvg = Math.round(
                  that.average(that.xinLvList)
                );
              } else {
                that.huxianshiZhi.xinLvMax = "--";
                that.huxianshiZhi.xinLvMin = "--";
                that.huxianshiZhi.xinLvAvg = "--";
              }
              // if(res.data==''){
              //   that.twHistoryData = [];
              // }else{
              //   that.twHistoryData = res.data.history;
              // }
              //   that.twHistoryData.map(function(item){
              //     item.map(function(it){
              //       var i = that.timestampToTime(it.timestamp);
              //       it.timestamp = i;
              //       that.timesList.push(it.timestamp.slice(5,16));
              //       that.tpecHeartRateList.push(it.heartRate)
              //       that.tpecRespiratoryRateList.push(it.respiratoryRate)
              //     })
              //   })
              //   console.log(that.twHistoryData)
              //
              //   var all1=0;
              //   var all2=0;
              //   var heartRateMax = that.tpecHeartRateList[0];
              //   var heartRateMin = that.tpecHeartRateList[0];
              //   var respiratoryRateMax = that.tpecRespiratoryRateList[0];
              //   var respiratoryRateMin = that.tpecRespiratoryRateList[0];
              //   that.tpecHeartRateList.map(function(item){
              //     all1 = all1+Number(item)
              //     heartRateMax = item > heartRateMax ? item : heartRateMax,
              //     heartRateMin = item < heartRateMin ? item : heartRateMin
              //   })
              //   that.tpecRespiratoryRateList.map(function(item){
              //     all2 = all2+Number(item)
              //     respiratoryRateMax = item > respiratoryRateMax ? item : respiratoryRateMax,
              //     respiratoryRateMin = item < respiratoryRateMin ? item : respiratoryRateMin
              //   })
              //   var heartRateAverage;
              //   if(that.tpecHeartRateList.length==0){
              //     heartRateAverage = 0;
              //   }else{
              //     heartRateAverage = Math.round(that.accDiv(all1,that.tpecHeartRateList.length))
              //   }
              //   var respiratoryRateAverage;
              //   if(that.tpecRespiratoryRateList.length==0){
              //     respiratoryRateAverage = 0;
              //   }else{
              //     respiratoryRateAverage = Math.round(that.accDiv(all2,that.tpecRespiratoryRateList.length))
              //   }
              //   that.tj = {
              //     heartRate:{
              //       max:heartRateMax,
              //       min:heartRateMin,
              //       average:heartRateAverage
              //     },
              //     respiratoryRate:{
              //       max:respiratoryRateMax,
              //       min:respiratoryRateMin,
              //       average:respiratoryRateAverage
              //     }
              //   }
                that.drawLineXLHX();
            }
          })
        },
        closeXd(xdStatus){
          that.$emit('getxdStatus',!that.xdStatus)
        },
        /*绘制图表*/
        drawLineXLHX(){
          let myChart = this.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
              tooltip: {
               trigger: 'axis',
              },
              grid:{//图标留白区域
                top:'14px',
                left:'60px',
                right:'30px',
                bottom:'33px'
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  axisLine:{symbol:['none', 'arrow']},
                  data: that.tpecTimesList,
                  axisLabel: {
                     rotate:0,
                  },
                },
              ],
              yAxis: {
                  min: 0,
                   max: 130,
                   interval: 10,
                  axisLabel: {
                  formatter: '{value}'
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
              }, {
                 type: 'inside',
                 start:0,
                 end:50
              }, {
                 start:0,
                 end:50
              },],
              series: [
                {
                  data: that.xinLvList,
                  name:that.$t("history.xl"),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#ff39fa",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'#ff39fa',
                      borderColor:'#ff39fa',
                    }
                  },
                },
                {
                  data: that.huxiLvList,
                  name:that.$t("history.hxl"),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"rgba(94,119,255,1)",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'rgba(94,119,255,1)',
                      borderColor:'rgba(94,119,255,1)',
                    }
                  },
                }
              ]
           });
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  flex-direction: column;
  .msg{
    display:flex;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(153,153,153,1);
      text-align: left;
      padding-left:22px;
      margin:18px 0 13px;
    }
  }
  .select-pd{
    padding-left:22px;
    display:flex;
    margin-bottom:14px;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(51,51,51,1);
      margin-right:10px;
      line-height:30px;
    }
    a{
      display:inline-block;
      padding:6px 24px;
      border-radius:17px;
      border:1px solid rgba(24,144,255,1);
      color:#1890FF;
      margin-right:22px;
    }
    .lsjl-active{
      background:rgba(24,144,255,1);
      color:#fff;
    }
  }
  .history-tj-abtm{
    padding:16px 0 30px 0;
    width:600px;
    display:flex;
    justify-content: space-between;
    margin:0 auto;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(38,38,38,1);
    }
  }
  .history-tj-xlhx{
    width:500px;
    margin:3px auto 0 auto;
    .xlhx-row{
      display:flex;
      justify-content: space-between;
      margin-bottom:16px;
      .ys{
        display:flex;
        justify-content: space-between;
        i{
          width:25px;
          height:3px;
          display:block;
          margin-top: 8px;
        }
        .xl{
          background:#ff39fa;
        }
        .hx{
          background:#5C89FF;
        }
        .ys-span{
          display:block;
          width:60px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(40,45,50,1);
        }
      }
      span{
        display:block;
        width:100px;
      }
    }
  }
}
</style>



