<template>
  <PopUp :flow='dcszStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeDcsz'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe63d;</i>
          <span class="title-span">{{$t("history.dcsz")}}</span>
        </div>
        <div class="content">
          <div class="dcsz-form" v-loading="loading" :element-loading-text="$t('history.downText')">

            <div class="dcsz-top"  v-if='currentHistorytype==1'>
              <div class="dcsz-top-left">
                <span>{{$t("history.dclx")}}：</span>
              </div>
              <div class="dcsz-top-right">
                <el-radio-group v-model="radio">
                  <el-radio :label="0">{{$t("history.eCGText")}}</el-radio>
                  <el-radio :label="1">{{$t("history.hRText")}}</el-radio>
                </el-radio-group>
              </div>
            </div>
            <div class="dcsz-top" v-if='radio==0 && currentHistorytype==1'>
              <div class="dcsz-top-left">
                <span>{{$t("history.dateselect")}}：</span>
              </div>
              <div class="dcsz-top-right">
                <el-date-picker
                  v-model="dcDate"
                  type="date"
                  :picker-options="pickerTime"
                  :placeholder="$t('history.noDate')">
                </el-date-picker>
              </div>
            </div>
            <div class="dcsz-top" v-if="!(radio==0 && currentHistorytype==1)">
              <div class="dcsz-top-left">
                <span>{{$t("history.jcsj")}}：</span>
              </div>
              <div class="dcsz-top-right">
                <span>{{$t("history.qysj")}} {{currentRecord.startTime}}</span>
                <span>{{$t("history.tzsj")}} {{currentRecord.endTime}}</span>
              </div>
            </div>
            <div class="dcsz-bottom" v-if='currentHistorytype==0'>
              <div class="dcsz-bottom-left">
                <span>{{$t("history.twd")}}：</span>
              </div>
              <div class="dcsz-bottom-right">
                <a href="javascript:void(0)" :class="dcszType==0?'a-active':''" @click="dcszType=0">{{$t("history.all")}}</a>
                <a href="javascript:void(0)" :class="dcszType==1?'a-active':''" @click="dcszType=1" v-if="hss==true">&ge;{{dcszLow}}℃</a>
                <a href="javascript:void(0)" :class="dcszType==2?'a-active':''" @click="dcszType=2" v-if="hss==true">&ge;{{dcszHigh}}℃</a>
                <a href="javascript:void(0)" :class="dcszType==1?'a-active':''" @click="dcszType=1" v-if="hss==false">&ge;{{dcszLowEn}}℉</a>
                <a href="javascript:void(0)" :class="dcszType==2?'a-active':''" @click="dcszType=2" v-if="hss==false">&ge;{{dcszHighEn}}℉</a>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='extendFn' v-if='!loading'>{{$t("history.dc")}}</a>
          <a href="javascript:void(0)" class="on" style="background:#BCBCBC" v-if='loading'>{{$t("history.dc")}}</a>
          <a href="javascript:void(0)" class="off" @click="closeDcsz">{{$t("button.close")}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { privateExportData,ecgExportData } from '@/api/history'
var that= {};
  export default {
    name: 'dcsz',
      data(){
          return {
            dcszLow:'36.8',
            dcszHigh:'38.0',
            dcszLowEn:'98.24',
            dcszHighEn:'100.4',
            dcszType:0,
            hss:that.$store.state.monitor.selectTem,
            radio:0,
            dcDate:'',
            loading:false,
            pickerTime: {
               disabledDate(time) {
                   var startTime = that.currentRecord.startTime.slice(0,10).replace(/-/g,'/');
                   var endTime = that.currentRecord.endTime.slice(0,10).replace(/-/g,'/');
                   var nowTime = that.timetrans(new Date().getTime()).replace(/-/g,'/');
                   var timestampStart = new Date(startTime).getTime();
                   var timestampEnd = new Date(endTime).getTime();
                   var timestampNow = new Date().getTime();
                   var date30 = 31*24*60*60*1000;
                   var timestamp30 = Number(timestampNow-date30);
                   var minDate = Number(timestampStart - timestamp30)>0?timestampStart:timestamp30;
                   if(that.currentRecord.recordStatus == 0){//使用中
                     var maxDate = Number(timestampNow-24*60*60*1000);
                   }else if(that.currentRecord.recordStatus == 1){//已结束
                     var maxDate = Number(timestampEnd - timestampNow)<0?nowTime == endTime?Number(timestampNow-24*60*60*1000):timestampEnd:Number(timestampNow-24*60*60*1000);//如果当前时间是结束时间，则选择当前时间的前一天
                   }
                   return time.getTime()<minDate || time.getTime()>maxDate;
               },
           },
          }
      },
      props:{
        dcszStatus:{
          type:Boolean
        },
        currentHistorytype: {
          type: Number
        },
        currentRecord:{
          type:Object
        },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
        closeDcsz(dcszStatus){
          that.$emit('getdcszStatus',!that.dcszStatus)
        },
        extendEcgFn(){//心电图导出
          if(!that.dcDate){
            that.error(that.$t("history.noDate"))
          }else{
            that.loading = true;
            var data = {
              baseId:that.currentRecord.baseId,
              timestamp:that.timetrans(that.dcDate)
            }
            ecgExportData(data).then((res)=>{
              if(res.data.rCode=='success'){
                that.success(that.$t("message.OperSuccess"));
                that.$emit('getdcszStatus',!that.dcszStatus)
                that.loading = false;
              }
            })
          }
        },
        extenddefaultFn(){//正常导出
          var temType;
          var language;
          if(that.$store.state.monitor.selectTem){
            temType = 0;
          }else{
            temType = 1;
          }
          if(localStorage.getItem('locale')=='en'){
            language = 1;
          }else{
            language = 0;
          }
          if(that.currentHistorytype==0){
            if(that.dcszType==0){
              var data={
                type:that.currentHistorytype,
                recordId:that.currentRecord.recordId,
                token:that.$store.state.login.token,
                temType:temType,
                language:language
              }
            }else if(that.dcszType==1){
              var data={
                type:that.currentHistorytype,
                recordId:that.currentRecord.recordId,
                temperature:that.dcszLow,
                token:that.$store.state.login.token,
                temType:temType,
                language:language
              }
            }else if(that.dcszType==2){
              var data={
                type:that.currentHistorytype,
                recordId:that.currentRecord.recordId,
                temperature:that.dcszHigh,
                token:that.$store.state.login.token,
                temType:temType,
                language:language
              }
            }
          }else{
            var data={
              type:that.currentHistorytype,
              recordId:that.currentRecord.recordId,
              token:that.$store.state.login.token,
              temType:temType,
              language:language
            }
          }
          privateExportData(data).then((res)=>{
            console.log(res)
            if(res.data.rCode=='success'){
              that.success(that.$t("message.OperSuccess"));
              that.$emit('getdcszStatus',!that.dcszStatus)
            }
          })
        },
        extendFn(){
          if(that.currentHistorytype == 1 && that.radio == 0){
            that.extendEcgFn();
          }else{
            that.extenddefaultFn();
          }
        },
        timetrans(d) {
          //时间戳转字符串 6位
          var date = new Date(d);
          var Y = date.getFullYear() + "-";
          var M =
            (date.getMonth() + 1 < 10
              ? "0" + (date.getMonth() + 1)
              : date.getMonth() + 1) + "-";
          var D =
            (date.getDate() < 10 ? "0" + date.getDate() : date.getDate());
          return Y + M + D;
        },

      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
 <style scoped lang="scss">
 .content{
    margin:0 42px;
    .dcsz-form{
      width:665px;
      height:215px;
      box-sizing: border-box;
      padding:40px 120px 53px 127px;
      .dcsz-top{
        display:flex;
        flex-direction: row;
        margin-bottom:32px;
        .dcsz-top-left{
          display:flex;
          flex-direction: row;
          justify-content: flex-end;
          span{
            font-size:16px;
             font-family:PingFangSC-Regular,PingFang SC;
             font-weight:400;
             color:rgba(102,102,102,1);
             line-height:22px;
          }
        }
        .dcsz-top-right{
          display:flex;
          flex-direction: column;
          span{
            font-size:16px;
            font-family:PingFangSC-Medium,PingFang SC;
            font-weight:600;
            color:rgba(35,35,35,1);
            line-height:22px;
            margin-bottom:5px;
            text-align: left;
          }
        }
        .dcsz-top-right /deep/ .el-radio{
          line-height: 22px;
        }
        .dcsz-top-right /deep/ .el-date-editor{
          width:260px;
          margin-top: -7px;
        }
        .dcsz-top-right /deep/ .el-input__inner{
          width:260px;
        }
      }
      .dcsz-bottom{
        display:flex;
        flex-direction: row;
        .dcsz-bottom-left{
          display:flex;
          align-items: center;
          justify-content: flex-end;
          span{
            font-size:16px;
           font-family:PingFangSC-Regular,PingFang SC;
           font-weight:400;
           color:rgba(102,102,102,1);
           line-height:22px;
          }
        }
        .dcsz-bottom-right{
          display:flex;
          flex-direction: row;
          a{
            display:block;
            width:97px;
            height:34px;
            line-height:34px;
            border-radius:18px;
            margin-right:21px;
            background:#fff;
            border:1px solid rgba(24,144,255,1);
            color:rgba(24,144,255,1);
          }
          a:nth-child(3){
            margin-right:0;
          }
          .a-active{
            background:rgba(24,144,255,1);
            border:1px solid rgba(24,144,255,1);
            color:#fff;
          }
        }
      }
    }
 }
 </style>



